import React from 'react'
import { SidebarContainer, CloseIcon, Icon, SidebarWrapper, SidebarMenu, SidebarLink } from './SidebarElements'

const Sidebar = ({isOpen, toggle}) => {
    return (
        <>
           <SidebarContainer isOpen={isOpen} onClick={toggle}>
               <Icon onClick={toggle}>
                   <CloseIcon />
               </Icon>
               <SidebarWrapper>
                   <SidebarMenu>
                       <SidebarLink to="about" onClick={toggle}>About</SidebarLink>
                       <SidebarLink to="education" onClick={toggle}>Education</SidebarLink>
                       <SidebarLink to="experience" onClick={toggle}>Experience</SidebarLink>
                       <SidebarLink to="portofolio" onClick={toggle}>Portofolio</SidebarLink>
                       <SidebarLink to="contact" onClick={toggle}>Contact</SidebarLink>
                   </SidebarMenu>
               </SidebarWrapper>
           </SidebarContainer>
        </>
    )
}

export default Sidebar
